import React from 'react'
import Link from 'gatsby-link'

const Management = () => (
  <div>
    <section className="nkc-banner hidden-print"></section>
    <main className="nkc-container clearfix">
      <section className="breadcumb hidden-print">
        <div className="container clearfix">
          <div className="col-lg-full col-md-full col-sm-full">
            <p><Link to="/">Home</Link> &rarr; Management</p>
          </div>
        </div>
      </section>
      <div className="clearfix">
        <div className="col-lg-xlarge col-md-xlarge col-sm-full col-print-full clearfix">
          <div className="nkc-container__content clearfix">
            <h1 className="nkc-page-title">Management</h1>
            <h2 id="campus">Governing Body</h2>
            <p className="nkc-para-intro">The Governing Body of North Kamrup College consists of academicians and social workers of repute who are drawn from the university, higher education institutions, community, guardians, and both teaching and non teaching staff of the college.</p>
            <p>The Governing Body is a statutory body to look after the management of the college in regard of academic, infrastructure and administrative functioning. Veteran Physicist Late Dr. Paban Chandra Mahanta (Dean, Faculty of Science, Gauhati University) was the first President of the Governing Body and Late Dr. Ananta Mohan Das, a Physician and a social worker of repute had the privilege of leading the institution as the Secretary of the public body (Governing Body).</p>
            <h2>Members of Governing Body</h2>
            <p>Its a ten members Governing Body led by Dr. Manjura Mohan Kalita as President.</p>
            <div className="responsive-table">
              <table className="nkc-data-table">
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>Designation</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>Dr. Manjura Mohan Kalita</td>
                    <td>President, Governing Body</td>
                  </tr>
                  <tr>
                    <td>Dr. Surajit Barman</td>
                    <td>Secretary, Governing Body (Ex-officio)</td>
                  </tr>
                  <tr>
                    <td>Dr. Satyendra Nath Kalita</td>
                    <td>Member, Governing Body (G. U. Nominee)</td>
                  </tr>
                  <tr>
                    <td>Dr. Prabhat Kalita</td>
                    <td>Member, Governing Body (G. U. Nominee)</td>
                  </tr>
                  <tr>
                    <td>Mr. Jiten Kalita</td>
                    <td>Member, Governing Body</td>
                  </tr>
                  <tr>
                    <td>Mr. Dibakar Nath</td>
                    <td>Member, Governing Body</td>
                  </tr>
                  <tr>
                    <td>Mrs. Sarala Boro</td>
                    <td>Member, Governing Body</td>
                  </tr>
                  <tr>
                    <td>Mr. P. K. Das</td>
                    <td>Faculty Member, Governing Body</td>
                  </tr>
                  <tr>
                    <td>Mr. P. Kalita</td>
                    <td>Faculty Member, Governing Body</td>
                  </tr>
                  <tr>
                    <td>Mr. M. Basumatary</td>
                    <td>Non Teaching Staff Member, Governing Body</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </main>
    <div className="spacer"></div>
  </div>
)

export default Management



